.App {
  text-align: center;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fefefe;
}

.hi {
  font-size: 6rem;
  color: #8a2be2; 
  cursor: pointer;
  transition: transform 0.2s;
  font-family: 'PT Sans', sans-serif;
  user-select: none;
}

.hi:hover {
  transform: scale(1.1);
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(240, 230, 255, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.popup-content {
  background: transparent; 
  border-radius: 12px;
  max-width: 90%;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  user-select: none;
}

.popup-content img,
.cat-video {
  max-width: 100%;
  max-height: 70vh;
  border-radius: 12px;
  box-shadow: 0 0 12px rgba(138, 43, 226, 0.7);
}

.meow-text {
  margin-top: 10px;
  color: #d8b8ff; 
  font-size: 1.5rem;
  font-family: 'PT Sans', sans-serif;
  cursor: pointer;
  user-select: none;
  transition: color 0.3s;
}

.meow-text:hover {
  color: #b36aff;
}
.meow-text {
  position: relative;
  display: inline-block;
  margin-top: 1rem;
  background-color: #d8b4f8; /* нежно-сиреневый */
  color: #fff;
  font-size: 1.2rem;
  padding: 12px 24px;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  font-family: 'PT Sans', sans-serif;
  transition: transform 0.2s ease, background-color 0.3s ease;
  box-shadow: 0 5px 15px rgba(140, 100, 200, 0.3);
}

.meow-text:hover {
  transform: scale(1.05);
  background-color: #cba2f0;
}

.meow-text::before,
.meow-text::after {
  content: '';
  position: absolute;
  top: -12px;
  width: 0;
  height: 0;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
  border-bottom: 20px solid #d8b4f8;
}

.meow-text::before {
  left: 8px;
  transform: rotate(-20deg);
}

.meow-text::after {
  right: 8px;
  transform: rotate(20deg);
}
